<template>
  <div id="app" class="bg-black h-screen select-none">
    <!-- Settings Modal -->
    <div
      class="
        fixed
        inset-0
        bg-gray-600 bg-opacity-50
        overflow-y-auto
        h-full
        w-full
        z-50
        text-center
        flex
        justify-center
        text-white
      "
      id="my-modal"
      v-show="showset"
    >
      <div
        class="bg-black rounded-lg border-2 mt-14 w-5/6 h-4/6 relative"
      >
        <div class="p-4">
          <input type="checkbox" id="timerRandom" class="form-checkbox rounded" v-model="timerRandom" /> 
          <label for="timerRandom"> Random Timer (&plusmn; 3 minutes)</label>
        </div>
        <div class="p-4">
          <input
            type="number"
            class="text-black w-12"
            min="0"
            max="120"
            v-model="timerMinutes"
            id="timerMinutes"
          />
          <label for="timerMinutes"> Timer Base Minutes</label>
          <p class="p-2">Randomized timer will count down from the minimum possible game time, then count up until full time has been reached</p>
        </div>
        <div class="p-4">Made by John DeBoer.  Find me on Facebook or Discord (systemphase#3338) with feedback or bug reports.</div>
        <div class="absolute bottom-0 inset-x-0">
          <button
            class="rounded-lg bg-emerald-700 p-2 px-16"
            @click="showset = false; resetTimer();"
          >
            Close
          </button>
        </div>
      </div>
    </div>

    <div class="w-full h-2/6">
      <!-- Clock Controls -->
      <div
        class="
          w-full
          h-1/6
          flex flex-row
          text-center
          items-center
          justify-center
        "
      >
        <div
          class="
            w-1/3
            h-full
            border
            rounded-none
            border-gray-500
            text-white
            flex
            items-center
            justify-center
          "
          @click="showset = true"
        >
          <i class="fas fa-gear"></i>
        </div>
        <div
          class="
            w-1/3
            h-full
            border
            rounded-none
            border-gray-500
            text-white
            flex
            items-center
            justify-center
          "
          @click="toggleTimer()"
        >
          <i class="fas" :class="timerIcon"></i>
        </div>
        <div
          class="
            w-1/3
            h-full
            border
            rounded-none
            border-gray-500
            text-white
            flex
            items-center
            justify-center
          "
          @click="resetTimer()"
        >
          <i class="fas fa-refresh"></i>
        </div>
      </div>

      <!-- Clock -->
      <div
        class="
          w-full
          h-5/6
          border-8 
          flex
          justify-center
          items-center
        "
        :class="timerStyle"
      >
        <div class="text-9xl not-italic font-semibold text-center">
          {{ timerText }}
        </div>
      </div>
    </div>
    <!-- Scoreboards -->
    <div
      class="
        w-full
        h-1/2
        flex flex-row
        text-white text-center text-6xl
        not-italic
      "
    >
      <div
        class="bg-red-700 w-1/2 items-center flex justify-center mr-1 relative"
      >
        <div @click="playerOneVP < 99 ? playerOneVP++ : 0">
          <i class="fas fa-angle-up absolute top-0 inset-x-0"></i>
        </div>
        <div class="font-semibold text-9xl">{{ playerOneScore }}</div>
        <div @click="playerOneVP > 0 ? playerOneVP-- : 0">
          <i class="fas fa-angle-down absolute inset-x-0 bottom-0"></i>
        </div>
      </div>
      <div
        class="bg-blue-700 w-1/2 items-center flex justify-center ml-1 relative"
      >
        <div @click="playerTwoVP < 99 ? playerTwoVP++ : 0">
          <i class="fas fa-angle-up absolute top-0 inset-x-0"></i>
        </div>
        <div class="font-semibold text-9xl">{{ playerTwoScore }}</div>
        <div @click="playerTwoVP > 0 ? playerTwoVP-- : 0">
          <i class="fas fa-angle-down absolute inset-x-0 bottom-0"></i>
        </div>
      </div>
    </div>

    <!-- Round Counter -->
    <div
      class="
        w-full
        h-1/6
        text-6xl
        not-italic
        font-semibold
        text-white text-center
        flex
        items-center
        justify-center
        flex-row
      "
    >
      <div
        class="h-full w-1/6 flex items-center justify-center"
        @click="roundNumber > 1 ? roundNumber-- : 0"
      >
        <i class="fas fa-minus"></i>
      </div>
      <div class="h-full w-4/6 flex items-center justify-center">
        Round {{ roundNumber }}
      </div>
      <div
        class="h-full w-1/6 flex items-center justify-center"
        @click="roundNumber < 20 ? roundNumber++ : 0"
      >
        <i class="fas fa-plus"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
      this.resetTimer(true);
  },
  data() {
    return {

      playerOneVP: 0,
      playerTwoVP: 0,

      roundNumber: 1,

      timerRunning: false,
      timerState: "normal",

      timerMinutes: 75,
      timerRandom: true,

      timerRemaining: 4500000, //75 minutes
      timerExtra: 0,
      timerEnd: null,
      timer: undefined,
      timeNow: null,
      timerText: "75:00",

      showset: false,
    };
  },
  computed: {
    maxId() {
      let maxId = 0;
      for (let ship of this.ships) {
        if (ship.id > maxId) {
          maxId = ship.id;
        }
      }
      return maxId;
    },
    playerOneScore() {
      return this.playerOneVP;
    },
    playerTwoScore() {
      return this.playerTwoVP;
    },

    timerIcon() {
      if (this.timerRunning) {
        return "fa-pause";
      } else {
        return "fa-play";
      }
    },

    timerStyle() {
      if (this.timerState === "normal") {
        return "border-black text-white";
      } else if (this.timerState === "extra"){
        return "border-yellow-400 text-yellow-400";
      } else {
          return "border-red-400 text-red-400";
      }
    },
  },
  methods: {
    toggleTimer() {
      this.timerRunning = !this.timerRunning;

      if (this.timerRunning) {
        // record end time
        this.timerEnd = Date.now() + this.timerRemaining;
        this.timer = setInterval(this.updateTimer, 500);
      } else {
        clearInterval(this.timer);
      }
    },
    resetTimer(check = false) {
      if (check || confirm("Reset Timer and Scores?")) {
        this.timerRunning = false;
        clearInterval(this.timer);
        this.timerState = "normal";

        if(this.timerRandom){
            this.randomizeTimer();
            this.timerRemaining = (this.timerMinutes-3) * 60000;
            this.timerText = `${this.timerMinutes-3}:00`;
        }
        else {
            this.timerRemaining = this.timerMinutes * 60000;
            this.timerText = `${this.timerMinutes}:00`;
        }

        this.playerOneVP = 0;
        this.playerTwoVP = 0;
        this.roundNumber = 1;
      }
    },

    updateTimer() {
        if (this.timerRunning) {
            this.timerRemaining = this.timerEnd - Date.now();
        }

        if(this.timerState === "normal")
        {
            if (this.timerRemaining <= 0) {
                if(this.timerRandom)
                {
                    this.timerState = "extra";
                }
                else
                {
                    this.timerState = "expired";
                }
                
            }

            let minutes = Math.floor(this.timerRemaining / (1000 * 60));
            let seconds = Math.floor(
                (this.timerRemaining - minutes * 60 * 1000) / 1000
            );
            this.timerText = `${minutes}:${String(seconds).padStart(2, "0")}`;
        }
        if(this.timerState === "extra")
        {
            let extraTime = this.timerRemaining * -1;
            if(extraTime >= this.timerExtra * 60000)
            {
                this.timerState = "expired";
            }

            let minutes = Math.floor(extraTime / (1000 * 60));
            let seconds = Math.floor(
                (extraTime - minutes * 60 * 1000) / 1000
            );
            this.timerText = `+${minutes}:${String(seconds).padStart(2, "0")}`;
        }
        if(this.timerState === "expired")
        {
            this.timerRunning = false;
            clearInterval(this.timer);
            this.timerText = "TIME!"

        }
    },

    randomizeTimer() {
        this.timerExtra = 3;
        let sign = 1;
        if(this.getRandomInt(2) === 1) { sign = -1;}
        for(let i = 0; i < 3; i++)
        {
            if(this.getRandomInt(8) >= 3) { this.timerExtra += sign; }
        }
//        console.log("timerExtra: " + this.timerExtra);
    },
    getRandomInt(max) {
        return Math.floor(Math.random() * max);
    },

  },
};
</script>
